import React from "react";
import UserContext from "../utils/UserContext";
class Profile extends React.Component {
  constructor(props) {
    super(props);
    //create state here
    this.state = {
      userInfo: {
        name: "Test User",
        avatar_url: "https://avatars.githubusercontent.com/u/106429788?v=4",
      },
    };
    console.log("child constructor()");
  }

  async componentDidMount() {
    const data = await fetch("https://api.github.com/users/rohitsharma02p");
    const json = await data.json();
    this.setState({
      userInfo: json,
    });
    console.log("child componentDidMount()");
  }
  render() {
    console.log("child render()");
    return (
      <div>
        <h1>Profile class component</h1>
        <UserContext.Consumer>
          {({ user }) => <h1>Email: {user.email}</h1>}
        </UserContext.Consumer>
        <div>Name:{this.props.name}</div>
        <div>Name from State:{this.state.userInfo.name}</div>
        <img src={this.state.userInfo.avatar_url} alt="missing profile" />
      </div>
    );
  }
}
export default Profile;

